import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Tabs } from 'ionic-angular';

/**
 * Generated class for the ParkingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  segment: 'parking'
})
@Component({
  selector: 'page-parking',
  templateUrl: 'parking.html',
})
export class ParkingPage {
  tab1Root = 'ParkingTenantPage';
  tab2Root = 'ParkingOrderPage';

  tab1Url = 'parkingTenant';
  tab2Url = 'parkingOrder';

  tab1Title = '经营单位管理';
  tab2Title = '停车场订单管理';

  @ViewChild('parkingTabs') tabRef: Tabs;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ParkingPage');
  }

}



